import { Link } from 'react-router-dom'

import { ReactComponent as CartIcon } from '../../../assets/img/cart-btn.svg'

const BtnToCart = () => {
	return (
		<Link to={'../cart'} className='button button--outline button__too-cart'>
			В корзине
			<CartIcon />
		</Link>
	)
}

export default BtnToCart
