import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import React from 'react'

import { setSearchValue } from '../../redux/search/Slice'
import { SortPropertyEnum } from '../../redux/filter/types'
import { setCurrentPage, setIndexCategory, setSelected } from '../../redux/filter/Slice'
import { selectorCart } from '../../redux/cart/Slice'

import { Search } from '../imports'
import logoSvg from '../../assets/img/pizza-logo.svg'
import { ReactComponent as CartIcon } from '../../assets/img/cart.svg'

const Header: React.FC = () => {
	const { totalCount, totalPrice, products } = useSelector(selectorCart)
	const isMounted = React.useRef(false)
	const dispatch = useDispatch()

	const onClickLogo = () => {
		setSearchValue('')
		dispatch(
			setSelected({
				name: 'популярности',
				sort: SortPropertyEnum.RATING,
			})
		)
		dispatch(setCurrentPage(1))
		dispatch(setIndexCategory(0))
	}

	React.useEffect(() => {
		if (isMounted.current) {
			const json = JSON.stringify(products)
			window.localStorage.setItem('cart', json)
		}
		isMounted.current = true
	}, [products])

	return (
		<div className='header'>
			<div className='container'>
				<Link to='/' className='header__logo' onClick={onClickLogo}>
					<img width='38' height='53' src={logoSvg} alt='Pizza logo' />
					<div>
						<h1>React Pizza</h1>
						<p>самая вкусная пицца во вселенной</p>
					</div>
				</Link>
				<Search />
				<div className='header__cart'>
					<Link to='/cart' className='button button--cart'>
						<span>{totalPrice} ₽</span>
						<div className='button__delimiter'></div>
						<CartIcon stroke='white' strokeWidth='1.8' strokeLinecap='round' strokeLinejoin='round' />
						<span>{totalCount}</span>
					</Link>
				</div>
			</div>
		</div>
	)
}

export default Header
