import cls from './Search.module.scss'
import { useDispatch, useSelector } from 'react-redux'
import React from 'react'

import { RootState } from '../../redux/store'
import { setSearchValue } from '../../redux/search/Slice'

import { ReactComponent as SearchCross } from '../../assets/img/searchCross.svg'
import { ReactComponent as SearchIcon } from '../../assets/img/search.svg'

const Search: React.FC = () => {
	const dispatch = useDispatch()
	const searchValue = useSelector((state: RootState) => state.search.searchValue)
	const inputRef = React.useRef<HTMLInputElement>(null)

	const setValue = (event: React.ChangeEvent<HTMLInputElement>) => {
		inputRef.current?.focus()
		dispatch(setSearchValue(event.target.value))
	}

	return (
		<form className='search__form' action='#'>
			<label className={cls.pizzas_search}>
				<SearchIcon className={cls.icon} />
				<input
					ref={inputRef}
					value={searchValue}
					className={cls.input}
					onChange={setValue}
					type='text'
					placeholder='Поиск пицц ...'
				/>
				{searchValue && (
					<SearchCross onClick={() => dispatch(setSearchValue(''))} className={cls.close_icon} />
				)}
			</label>
		</form>
	)
}

export default Search
