import './_pizza-block.scss'
import { Link } from 'react-router-dom'
import { useSelector } from 'react-redux'
import React from 'react'

import { RootState } from '../../redux/store'

import BtnToCart from './Btn`s/BtnToCart'
import BtnAddPizza from './Btn`s/BtnAddPizza'

export const typeNames = ['Тонкое', 'Традиционное']

export type pizzaItem = {
	id: string
	imageUrl: string
	title: string
	price: number
	sizes: number[]
	types: number[]
}

const PizzaItem: React.FC<pizzaItem> = (pizza) => {
	const [activeType, setActiveType] = React.useState(0)
	const [activeSize, setActiveSize] = React.useState(0)

	const cartItem = useSelector((state: RootState) => state.cart.products.find((obj) => obj.id === pizza.id))
	const addedCount = cartItem ? cartItem.count : 0

	return (
		<div className='pizza-block'>
			<Link to={`pizza/${pizza.id}`} className='pizza-block__image'>
				<div className='pizza-block__image'>
					<img width='260' height='260' src={pizza.imageUrl} alt='Pizza' />
				</div>
				<h3 className='pizza-block__title'>{pizza.title}</h3>
			</Link>
			<div className='pizza-block__selector'>
				<ul>
					{pizza.types.map((typeId, i) => {
						return (
							<li
								key={i}
								onClick={() => setActiveType(typeId)}
								className={activeType === i ? 'active' : ''}
							>
								{typeNames[typeId]}
							</li>
						)
					})}
				</ul>
				<ul>
					{pizza.sizes.map((size, i) => {
						return (
							<li
								key={i}
								onClick={() => setActiveSize(i)}
								className={activeSize === i ? 'active' : ''}
							>
								{size} см.
							</li>
						)
					})}
				</ul>
			</div>
			<div className='pizza-block__bottom'>
				<div className='pizza-block__price'>от {pizza.price} ₽</div>
				{addedCount === 0 ? (
					<BtnAddPizza
						id={pizza.id}
						imageUrl={pizza.imageUrl}
						title={pizza.title}
						price={pizza.price}
						sizes={pizza.sizes}
						types={pizza.types}
						activeSize={activeSize}
						activeType={activeType}
					/>
				) : (
					<BtnToCart />
				)}
			</div>
		</div>
	)
}

export default PizzaItem
